<template>
	<section>
		<div v-if="hasPersonMsg" class="my_container c">
			<aside class="my_person_data">
				<p>No.19851122</p>
				<div><img src="" alt=""></div>
				<span>点击更换照片</span>
			</aside>
			<ul class="my_person_msg">
				<li>Weidade Zhuxi Xijinping</li>
				<li>
					<p><span>性别：</span><span class="my_ps_msg_f">男</span></p>
					<p class="my_tips_en">Sex</p>
				</li>
				<li>
					<p><span>国家地区：</span><span class="my_ps_msg_f">中国 北京 东城</span></p>
					<p class="my_tips_en">Country</p>
				</li>
				<li>
					<p><span>证件号码；</span><span class="my_ps_msg_f">11010119851122051X</span></p>
					<p class="my_tips_en">ID Number</p>
				</li>
				<li>
					<p><span>手机号码：</span><span class="my_ps_msg_f">+186 13552118712 <i class="iconfont">&#xe645;</i></span></p>
					<p class="my_tips_en">Phone Number</p>
				</li>
			</ul>
		</div>
		<div v-else class="my_container_nomsg">
			<button type="button" class="my_container_btn">填写资料</button>
		</div>
		<ul class="my_list">
			<li class="c" v-clickEffectLi><span class="my_mg my_list_msg">资格管理</span><span class="my_list_msg_btn">150个</span></li>
			<li class="c" v-clickEffectLi @click="gotoRegister"><span class="my_register my_list_msg">WPT在线注册</span><span class="my_list_msg_btn"></span></li>
		</ul>
		<div v-clickEffectCheckcard @click="showCheckCardHandler" class="check_card_into">
			检录牌
		</div>
		<Nav-bar :active="1"></Nav-bar>
	</section>
</template>

<script>
	// import NavBar from '@/page/Nav';
	const NavBar = r => require.ensure([],() => r(require('@/page/nav')),'group-my');
	export default {
		data(){
			return {
				hasPersonMsg:true
			}
		},
		methods:{
			gotoRegister(){
				if(this.hasPersonMsg){
					this.$router.push('register');
					return;
				}
			},
			showCheckCardHandler(){
				this.$router.push({
					name:'checkcard',
					params:{
						next:'my'
					}
				})
			}
		},
		directives:{
			clickEffectLi:{
				bind(el){
					el.addEventListener('touchstart',function(){
						console.log(1);
						this.style.background = "rgba(0,0,0,.1)"
					})
					el.addEventListener('touchend',function(){
						this.style.background = "rgba(0,0,0,0)"
					})
				}
			},
			clickEffectCheckcard:{
				bind(el){
					el.addEventListener('touchstart',function(){
						this.style.background = "rgba(62,168,141,0.6)"
					})
					el.addEventListener('touchend',function(){
						this.style.background = "rgba(62,168,141,1)"
					})
				}
			}
		},
		components:{
			NavBar
		}
	}
</script>

<style scoped lang="scss">
	@import './../assets/sass/function.scss';
	section{
		color:#717989;
		padding-bottom:38px;
	}
	.my_container{
		padding-bottom:20px;
		font-size:px2em(24px);
		border-bottom:1px solid #151820;
	}
	.my_container_nomsg{
		position:relative;
		min-height:200px;

	}
	.my_container_btn{
		position:absolute;
		top:0;left:0;right:0;bottom:0;
		margin:auto;
		width:px2em(300px);
		height:px2em(80px);
		font-size:px2em(30px);
	}
	.my_person_data{
		width:px2em(290px);
		float:left;
		text-align:center;
		p{
			margin-top:16px;
		}
		img{
			display:inline-block;
			padding:30px 0;
			width:px2em(200px);
			height:px2em(200px);
		}
	}
	.my_person_msg{
		float:left;
		li{
			margin-top:16px;
		}
	}
	.my_ps_msg_f{
		position:relative;
		color:#ffffff;
		i{
			font-size:px2em(50px);
			line-height:0.5;
			color:#748cad;
			vertical-align:middle;
		}
	}
	.my_tips_en{
		font-size:px2em(22px);

	}
	.my_list{
		border-bottom:1px solid #2f3647;
		li{
			width:100%;
			padding:20px 0;
			border-top:1px solid #2f3647;
			border-bottom:1px solid #151820;
			font-size:px2em(24px);
		}
	}
	.my_list_msg{
		float:left;
		text-indent:px2em(40px);
	}
	.my_list_msg_btn{
		float:right;
		width:px2em(120px);
		margin-right:px2em(40px);
		height:18px;
		color:#ffffff;
		background:url(./../assets/images/small_arr_right.png) no-repeat right center / px2em(15px) px2em(30px);
	}
	.check_card_into{
		position:fixed;
		bottom:60px;
		right:20px;
		height:60px;
		width:60px;
		text-align:center;
		line-height:60px;
		border-radius:50%;
		box-shadow:0 0 0 6px #2d655d;
		background:#3ea88d;
		color:#ffffff;
	}
</style>


